/**
 * License Screen.
 */
.network-admin.admin_page_wholesome-network-enabled-plugins {
    .fs-multisite-options-container {
        display: none;
    }
}

/** 
 * Dashboard.
 */
.wholesome-network-active-plugins {
    display: block;
    margin-top: 14px;

    &__label {
        color: #000000;
    }
}

body.plugins-php.network-admin {
    .row-actions span.delete .wholesome-network-active-plugins {
        a {
            color: #2271b1;
            &:hover {
                color: #135e96;
            }
        }
    }

    .plugins {
        .active.active--network {
            th.check-column,
            td {
                background-color: #fcf9e8;
            }

            th.check-column {
                border-color: #f5e6ab;
            }
        }

        .active.active--network + .plugin-update-tr td {
            background-color: #fcf9e8;
            border-color: #f5e6ab;
        }
    }

    .network-enabled-plugins__row {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        padding-top: 5px;
        padding-bottom: 5px;
    
        label {
            flex: 1;
            padding-right: 5px;
        }
    }
}



/**
 * Buttons.
 */
button.network-enabled-plugins {
    background-color: unset;
    border: unset;
    color: #2271b1;
    cursor: pointer;
    margin: unset;
    padding: unset;

    &:hover, &:focus {
        color: #135e96;
    }

    &:focus {
        box-shadow: 0 0 0 1px #4f94d4, 0 0 2px 1px rgb(79 148 212 / 80%);
        outline: 1px solid transparent;
    }
}

/**
 * Panel.
 */
.network-enabled-plugins__toggle-panel {
    overflow: hidden;
}

/**
 * UI.
 */

/**
 * Toggle.
 */
.network-enabled-plugins__icon {
    color: #bbb;
    cursor: pointer;
    font-size: 90%;
    margin-left: 5px;
    position: relative;
}

input[type="checkbox"].network-enabled-plugins__toggle {
    -webkit-appearance: none;
	-webkit-tap-highlight-color: transparent;
    background-color: transparent;
    border: 0;
    box-shadow: unset;
    cursor: pointer;
    margin-right: 24px;
    outline: 0;
	position: relative;
	
    &:focus {
        box-shadow: unset;
    }

    &:after {
        background: rgba( 196, 195, 195, 0.55 );
        border-radius: 20px;
        clear: both;
        content: '';
        display: inline-block;
        height: 22px;
        margin-right: 20px;
        width: 38px;
    }

    &:before {
        background: rgb( 255, 255, 255 );
        border-radius: 50%;
        box-shadow: 1px 1px 1px rgba( 0, 0, 0, 0.6 );
        content: '';
        display: block;
        height: 20px;
        left: 1px;
        position: absolute;
        top: 1px;
        width: 20px;
    }

    &:checked:after {
        background: #16a085;
    }

    &:checked:before {
        left: 21px;
        top: 4px;
    }
}

/**
 * Slide Down
 */
.slide-down {
    transition: height .25s ease-out;
    height: 0;
    overflow: hidden;
}

/**
* Slide Up
*/
.slide-up {
    transition: height .25s ease-out;
    overflow: hidden;
}